.task-modal{
  .upgrade-modal__price{
    .m-coin{
      img{
        width: 2em;
      }
    }
  }
  &__btn{
    margin: .75em auto 0;
    padding: 0 2.5em;
    height: 2em;
  }
}

.reward-modal{

}

.rewards{
  margin-top: .75em;
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em / 4));
    gap: .3125em;
  }
}

.reward-card{
  border-radius: .75em;
  border: 2px solid #f7f9fb;
  padding: .75em;
  display: flex;
  align-items: center;
  flex-direction: column;
  opacity: 0.5;
  background: #f7f9fb;
  &__icon{
    margin: .25em 0;
    width: 1.6875em;
    img{
      width: 100%;
      display: block;
    }
  }
  .text12,
  .text11{
    letter-spacing: 0.01em;
  }
  &._active{
    opacity: 1;
    border-color: $primary;
  }
  &._done{
    color: $white;
    opacity: 1;
    border: none;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: inherit;
      background: $primary;
      opacity: .25;
    }
    *{
      position: relative;
      z-index: 2;
    }
  }
}

.m-alert{
  position: fixed;
  top: 1em;
  left: 50%;
  transform: translateX(-50%);
  max-width: 98vw;
  padding: 0 .5em;
  background: #10c95a;
  height: 2.25em;
  display: flex;
  align-items: center;
  border-radius: .5em;
  white-space: nowrap;
  color: $white;
  &--red{
    background: #ff1b51;
  }
  &--blue{
    background: $primary ;
  }
  &__icon{
    @include center;
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .25em;
    img{
      width: 100%;
    }
  }
}

.m-loader{
  animation: spin 2s infinite linear;
}

@keyframes spin {
  0%  {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}
