.level-tabs{
  display: flex;
  margin: .75em 0;
  padding: .375em .5em;
  background-color: #F4F4F4;
  border-radius: .5em;
  .tab{
    padding: 0;
    width: 50%;
    flex-grow: unset;
    font-weight: 500;
    border: none;
    span{
      font-size: .75em;
    }
  }
}

.mine{
  .main-stat{
    margin-bottom: 1.125em;
  }
}

.upgrade-profit__items2{
  @include grid100gap(.25em);
  margin-top: 1em;
}

.task-card{
  padding: .75em;
  border-radius: .5em;
  border: 1px solid $primary;
  background-color: $white;
  display: flex;
  align-items: center;
  &__icon{
    background: rgba($primary,.5);
    border-radius: 50%;
    width: 2.25em;
    height: 2.25em;
    @include center;
    flex-shrink: 0;
    margin-right: .75em;
    svg,
    img{
      width: 1.5em;
    }
    path{
      stroke: $primary;
    }
  }
  .m-coin{
    margin-top: .25em;
    img{
      width: .875em;
      margin: 0 .25em;
    }
  }
  &__action{
    width: 2em;
    height: 2em;
    margin-left: auto;
    background-image: url(../img/arrow.svg);
    @include bgImgSize(100%);
    flex-shrink: 0;
  }
  &__btn{
    margin-left: auto;
    height: 1.5em;
    padding: 0 .625em;
    border-radius: .375em;
    display: flex;
    align-items: center;
    font-weight: 500;
    border: 1px solid $primary;
    img{
      width: .875em;
      margin-left: .25em;
    }
  }
}


.props{
  margin-top: 1.5em;
  @include grid100gap(.75em);
}

.prop-item{
  display: flex;
  justify-content: space-between;
  &__title,
  &__desc{
    font-size: .75em;
    line-height: 125%;
  }
  &__title{
    color: #B0B7B4;
    &--bold{
      color: $black;
      font-weight: 600;
    }
  }
  &__desc{
    font-weight: 600;
  }
  &__nums{
    display: flex;
  }
  &__num,
  &__num2{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: .875em;
      margin: 0 .25em;
      &:first-child{
        margin-left: 0;
      }
    }
  }
  &__num2{
    margin-left: .125em;
    img{
      margin-top: -.1875em;
    }
  }
}

.props2{
  border-top: 1px solid #F4F4F4;
  padding-top: .75em;
  margin-top: .75em;
  @include grid100gap(.375em);
  overflow: hidden;
}

.progress-bar{
  background-color: rgba($primary,.15);
  height: .875em;
  border-radius: 6.25em;
  position: relative;
  overflow: hidden;
  span{
    max-width: 100%;
    display: block;
    height: 100%;
    background-color: $primary;
  }
}


