.main {
    .main-sum {
        position: relative;
        z-index: 2;
    }
}

.main-top {
    &__user {
        flex-direction: column;
        &-img {
            border-radius: 50%;
            width: 3em;
            margin-bottom: .25em;
        }
    }
}

._full {
    grid-column: 1/-1;
}

._flex-center,
._flex-between,
._flex {
    display: flex;
    align-items: center;
}

._flex-between {
    justify-content: space-between;
}

._flex-center {
    justify-content: center;
}

.main-stat {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - .375em));
    gap: .75em;
    margin-top: .75em;
}

.stat-item {
    border-radius: .5em;
    padding: .5em;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid $primary;
    &__info {
        margin-top: .25em;
        display: flex;
        align-items: center;
    }
    &__coin {
        width: 1.25em;
        margin-right: .25em;
    }
    .hint {
        margin-left: .25em;
    }
}

.hint {
    &__btn {
        width: 1.25em;
        @include center;
        img {
            width: 100%;
        }
    }
}

.main-sum {
    margin-top: 2.5em;
    font-weight: 600;
    &__text {
        font-size: 2.25em;
    }
    img {
        width: 3.5em;
        margin-right: .75em;
    }
}

.main-btn {
    width: 100%;
    max-width: 10em;
    margin: .125em auto 0;
    position: relative;
    &:before {
        content: '';
        @include centerAbsolute;
        width: 208.33%;
        height: 208.33%;
        background-image: url(../img/rounds.svg);
        @include bgImgSize(100%);
        pointer-events: none;
    }
    img {
        position: relative;
        z-index: 2;
        width: 100%;
    }
    &:active {
        transform: rotate(4deg);
    }
}

.menu {
    background: #f7f9fb;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: .5em;
    border-radius: .75em .75em 0 0;
    z-index: 10;
    display: flex;
    &__btn {
        min-height: 3.125em;
        width: calc(20% - .75em / 5);
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-grow: 1;
        margin-right: .25em;
        background: #fff;
        border-radius: .5em;
        padding: .375em;
        color: $gray;
        &:last-child {
            margin-right: 0;
        }
        svg {
            height: 1.5em;
            margin-bottom: .125em;
        }
        span {
            font-size: .6875em;
            letter-spacing: 0.01em;
            line-height: 118%;
        }
        &:hover,
        &._active {
            pointer-events: none;
            background: $primary;
            color: $white;
            ._stroke {
                stroke: $white;
            }
            path:not(._stroke) {
                fill: $white;
            }
        }
    }
}

.main-bottom {
    z-index: 3;
    position: fixed;
    left: 0;
    bottom: 4.875em;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1em;
}

.main-energy {
    display: flex;
    align-items: center;
    font-weight: 500;
    img {
        width: 1em;
        margin-right: .375em;
    }
}

.main-progress {
    background: rgba(5, 172, 255, 0.4);
    height: 1.0625em;
    width: 100%;
    position: relative;
    border-radius: 62.5em;
    margin-top: .5em;
    padding: .125em;
    span {
        display: block;
        height: 100%;
        background: #e1f5ff;
        border-radius: inherit;
    }
}