.referral-text{
  @include grid100gap(.25em);
}

.referral-action{
  margin-top: .75em;
  @include grid100gap(.25em);
}

.referral-btn,
.friend-item{
  padding: .75em;
  background: #f7f9fb;
  border-radius: .5em;
  display: flex;
  align-items: center;
  border: 1px solid $primary;
}

.referral-btn{
  &__icon{
    width: 3em;
    margin-right: .75em;
  }
  &__wrap{
    margin-top: .25em;
  }
  .m-coin{
    margin-right: .125em;
    img{
      width: 1.25em;
      margin-right: .25em;
    }
  }
}

.friends{
  margin-top: .75em;
  &__top{
    margin-bottom: .5em;
  }
  &__refresh{
    width: 1.5em;
    @include center;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
  &__items{
    @include grid100gap(.25em);
  }
}

.friend-item{
  &__img{
    width: 2.3125em;
    height: 2.3125em;
    object-fit: cover;
    border-radius: 50%;
    margin-right: .75em;
  }
  &__wrap{
    margin-top: .25em;
  }
  .m-coin{
    img{
      width: 1em;
      margin-right: .25em;
    }
  }
  &__benefit{
    margin-left: auto;
    padding-left: 1em;
    color: #10c95a;
    font-weight: 600;
  }
}

.friends-empty{
  background: #f7f9fb;
  border-radius: .5em;
  padding: 2em;
  @include center;
  flex-direction: column;
  text-align: center;
  .text11{
    margin-top: .33em;
  }
  &__icon{
    @include center;
    border-radius: 50%;
    margin-bottom: .75em;
    background-color: $white;
    width: 2.75em;
    height: 2.75em;
    img{
      width: 1.5em;
    }
  }
}

.referral-action2{
  width: 100%;
  display: grid;
  grid-template-columns: calc(100% - 3.25em) 2.75em;
  gap: .5em;
  margin-top: .75em;
  &__btn{
    img{
      width: 1.5em;
      margin-right: .5em;
    }
    &._small{
      img{
        margin: 0;
        width: 2em;
      }
    }
  }
}
