.tabs{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .375em));
  gap: .75em;
}

.tab{
  border-radius: .25em;
  flex-grow: 1;
  height: 2em;
  padding: 0 1em;
  @include center;
  position: relative;
  font-weight: 500;
  color: $gray;
  transition: all .3s;
  border: 1px solid rgba(152, 161, 192, 0.3);
  span{
    font-size: 1em;
    line-height: 120%;
  }
  &:hover{
    color: $primary;
    border-color: $primary;
    cursor: pointer;
  }
  &._active{
    border-color: $primary;
    background: $primary;
    color: $white;
    pointer-events: none;
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}
