.success-modal{
  .modal__dialog{
    @include centerAbsolute;
    max-width: calc(100% - 1.5em);
    max-height: unset;
    bottom: unset;
    border: 1px solid rgba(#282928,.1);
    background: #fff;
    border-radius: 1em;
    padding: .75em .75em 1.25em;
    text-align: center;
  }
  .modal__content{
    padding: 0;
    @include grid100gap(1em);
  }
  .m-loader,
  &__icon{
    width: 3.5em;
    display: block;
    margin: 0 auto;
  }
}

.buy-upgrade-modal{
  .modal__dialog{
    padding-left: 1.75em;
    padding-right: 1.75em;
  }
  &__icon{
    width: 3.5em;
    display: block;
    margin: 0 auto;
  }
  &__desc{
    max-width: 14.5em;
    margin: 0 auto;
    font-weight: 500;
    color: $primary;
    text-align: center;
  }
  &__btn{
    width: 100%;
  }
}

.pagination{
  display: flex;
  justify-content: center;
  margin-top: .75em;
  &__link{
    @include center;
    font-size: .875em;
    width: 2.29em;
    height: 2.29em;
    border-radius: .28em;
    border: 1px solid $gray;
    margin: 0 .25em;
    color: $black;
    &._disabled{
      background-color: #F4F4F4;
      border-color: #F4F4F4;
    }
    &._active{
      background-color: $primary;
      border-color: $primary;
      color: $white;
    }
    svg{
      width: .5em;
    }
  }
}

.referral-action2{
  .fg{
    height: 2.75em;
    input{
      background-color: transparent;
      height: 100%;
      border: 1px solid $primary;
      border-radius: .57em;
      padding: 0 1em;
      color: $black;
    }
  }
  &__btn{
    svg{
      width: 1.25em;
    }
  }
}

.history{
  &__items{
    @include grid100gap(.75em);
    max-height: calc(100 * var(--vh) - 9em);
    overflow-y: auto;
  }
}

.history-card{
  padding: .75em;
  border: $border;
  border-radius: .5em;
  @include grid100gap(1em);
  &__row{
    display: flex;
    justify-content: space-between;
  }
  &__title,
  &__desc{
    font-size: .875em;
    line-height: 121%;
  }
  &__title{
    color: $gray;
  }
  &__desc{
    font-weight: 500;
    display: flex;
    align-items: center;
    &._blue{
      color: $primary;
    }
    img{
      width: 1em;
      margin-right: .28em;
    }
  }
}

.m-page{
  padding-top: 0;
}

.upgrade-profit__title{
  font-weight: 600;
  font-size: 1em;
  line-height: 130%;
  margin-bottom: .25em;
}

.upgrade-card{
  background-color: $white;
  .m-coin{
    margin-right: .25em;
    img{
      margin-left: .25em;
      &:first-child{
        margin-left: 0;
      }
    }
  }
}
