.mine{
  .stat-item{
    border-color: $primary;
    border-width: 2px;
  }
  .main-sum{
    margin: 0 0 .75em;
  }
}

.upgrade-profit{
  margin-top: .75em;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .25em .75em;
    margin-top: .5em;
    &._type2{
      grid-template-columns: 100%;
    }
  }
}

.upgrade-card{
  padding: .75em;
  border-radius: .5em;
  border: 1px solid $primary;
  display: flex;
  align-items: center;
  &._type2{
    background-image: url(../img/arrow3.svg);
    background-size: 1.5em auto;
    background-repeat: no-repeat;
    background-position: right .75em center;
  }
  &._done{
    background-image: url(../img/check.svg);
  }
  &__icon{
    background: rgba(53, 188, 254, 0.5);
    border-radius: 50%;
    width: 2.3125em;
    height: 2.3125em;
    @include center;
    flex-shrink: 0;
    margin-right: .5em;
    img{
      width: 1.5em;
    }
  }
  &__title{
    font-size: .875em;
    line-height: 120%;
    font-weight: 600;
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: .25em;
    .m-coin{
      img{
        width: 1em;
        margin-right: .25em;
      }
    }
  }
}

.m-coin{
  display: flex;
  align-items: center;
  img{
    width: 1.25em;
    margin-right: .25em;
  }
}

.upgrade-modal{
  .upgrade-card__icon{
    width: 6.25em;
    height: 6.25em;
    margin: 0 0 .5em;
    background: #f7f9fb;
    border-radius: 1.5em;
    img{
      width: 4em;
    }
    &._type2{
      box-shadow: unset;
      background: transparent;
      img{
        width: 100%;
      }
    }
  }
  .modal__info{
    padding: 0;
  }
  &__desc{
    margin-top: .5em;
  }
  &__price{
    margin-top: .75em;
    .m-coin{
      img{
        width: 1.5em;
      }
    }
  }
}
