.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    backdrop-filter: blur(10px);
    background: rgba(61, 61, 61, 0.4);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        max-height: calc(100vh - 3em);
        background: #fff;
        z-index: 3;
        background: linear-gradient(159.80deg, #838FFEFF 2.44%,#7BD4FDFF 200.549%);
        padding: .4375em 0 0;
        border-radius: 1em 1em 0 0;
        display: inline-block;
    }
    &__content{
        background: $white;
        border-radius: inherit;
        padding: 1.25em .75em 1.75em;
    }
    &__btn{
        margin-top: .75em;
        width: 100%;
    }
    .close-btn{
        position: absolute;
        right: .75em;
        bottom: 100%;
        margin-bottom: .375em;
        &._type2{
            top: .75em;
            bottom: unset;
            margin-bottom: 0;
        }
    }
}

.close-btn{
    width: 2em;
    @include center;
    &:hover{
        path{
            stroke: $primary;
        }
    }
    svg{
        width: 100%;
    }
}

.modal,
.increase-profit-modal,
.thank-modal{
    &__info{
        padding: 1em;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}

.thank-modal{
    .main-sum{
        margin: 0 0 .5em;
    }
    .modal__btn{
        margin-top: .75em;
    }
}

.increase-profit-modal{
    &__info{
        padding-left: 2em;
        padding-right: 2em;
        img{
            width: 6.5em;
            margin-bottom: 1em;
        }
        .text32{
            margin-bottom: 1em;
        }
    }
    &__desc{
        margin-top: 1.5em;
    }
}

.finance-modal{
    .modal__dialog{
        padding: 0;
        background: unset;
        border: 1px solid rgba(152, 161, 192, 0.1);
    }
    .modal__content{
        padding: 1.5em .75em;
    }
    .tabs{
        margin-bottom: 1.5em;
    }
    .wallet{
        margin: 1.5em 0;
    }
    .fg,
    .warning{
        margin-top: .75em;
    }
    &__hint{
        margin-top: .25em;
    }
}

.wallet{
    display: grid;
    grid-template-columns: calc(100% - 3.625em) 3.25em;
    gap: .375em;
    &__copy,
    &__code{
        background: #111524;
        border-radius: .75em;
        height: 3.25em;
        display: flex;
        align-items: center;
    }
    &__code{
        padding: 0 1.25em;
    }
    &__copy{
        justify-content: center;
        &:hover{
            g{
                opacity: 1;
            }
        }
    }
}

.coin-field{
    margin-top: .25em;
    position: relative;
    &:first-child{
        margin-top: 0;
    }
    &__content{
        position: absolute;
        left: .75em;
        top: 50%;
        transform: translateY(-50%);
    }
    &__currency{
        flex-shrink: 0;
        @include center;
        width: 2em;
        height: 2em;
        margin-right: .75em;
        img{
            width: 100%;
            display: block;
        }
    }
    &__title{
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 500;
    }
    &__bottom{
        margin-top: .125em;
        display: flex;
        align-items: center;
    }
    &__btn{
        margin-left: .25em;
        height: 1.125em;
        padding: 0 .3125em;
        @include center;
        background: rgba($primary, 0.1);
        color: $primary;
        border-radius: .25em;
        font-weight: 500;
        text-transform: uppercase;
        span{
            font-size: .625em;
        }
    }
    input{
        width: 100%;
        font-size: 1.75em;
        height: 2.21em;
        border: none;
        background: #f7f9fb;
        border-radius: .43em;
        text-align: right;
        padding-right: .43em;
        padding-left: 6.44em;
        color: $black;
        &::placeholder{
            color: $gray;
        }
    }
}

.ton-address{
    margin-top: .25em;
    background: rgba(5, 172, 255, 0.1);
    border-radius: .25em .25em .5em .5em;
    &__top{
        min-height: 1.375em;
        display: flex;
        align-items: center;
        padding: .3125em;
    }
    &__bottom{
        background: #f7f9fb;
        border-radius: .5em;
        padding: 1em .5em;
    }
}

.warning{
    padding: .5em;
    background: linear-gradient(159.80deg, rgba(72, 123, 255, 0.4) 22.887%,rgba(70, 39, 255, 0.4) 105.968%);
    border-radius: .75em;
    display: flex;
    align-items: center;
    img{
        width: 1.5em;
        margin-right: .5em;
    }
}
