@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 2.75em;
    border-radius: .5em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
    }
}

.m-btn-blue{
    background: $primary;
    color: $white;
    &:hover{
        background: darken($primary,10%);
        color: $white;
    }
}
